/* ===================================
	 main
	 @mixin ================================== */
.main {
	&_home {
		margin-top: -340px; } }

/* ===================================
	 logo
	 @mixin ================================== */
.logo {
	display: block;
	&_white {
		flex: 0 0 160px;
		@include s('logo'); }
	&_shadow {
		@include s('logo-shadow'); }
	&_color {
		@include r('logo-color'); } }


