$icons: (0:0);
$icons: map-merge($icons,('dev': (X: -294px, Y:-338px, W: 16px, H: 20px, TW: 601px, TH: 525px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('glow': (X: -502px, Y:-272px, W: 17px, H: 19px, TW: 601px, TH: 525px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('logo-color': (X: -332px, Y:-272px, W: 160px, H: 41px, TW: 601px, TH: 525px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('logo-shadow': (X: -124px, Y:-272px, W: 198px, H: 56px, TW: 601px, TH: 525px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('logo': (X: -124px, Y:-338px, W: 160px, H: 40px, TW: 601px, TH: 525px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('why-us-1': (X: 0px, Y:-272px, W: 114px, H: 253px, TW: 601px, TH: 525px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('why-us-2': (X: 0px, Y:0px, W: 153px, H: 262px, TW: 601px, TH: 525px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('why-us-3': (X: -163px, Y:0px, W: 152px, H: 255px, TW: 601px, TH: 525px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('why-us-4': (X: -325px, Y:0px, W: 135px, H: 255px, TW: 601px, TH: 525px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('why-us-5': (X: -470px, Y:0px, W: 131px, H: 255px, TW: 601px, TH: 525px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }

