.why-us {
	position: relative;
	background: #f2fafd;
	text-align: center;
	&__container {}
	&__inner {
		position: relative;
		z-index: 2;
		display: flex;
		margin: 46px auto 50px; }
	&__title {
		position: relative;
		z-index: 2;
		cursor: default; }
	&__item {
		position: relative;
		flex: 1 1 20%;
		&:nth-child(1) &-glow:nth-child(2) {
			top: 5%;
			left: 25%; }
		&:nth-child(1) &-glow:nth-child(3) {
			top: 27%;
			right: 42%; }
		&:nth-child(1) &-glow:nth-child(4) {
			top: 58%;
			left: 27%; }

		&:nth-child(2) &-glow:nth-child(2) {
			top: 2%;
			left: 43.5%; }
		&:nth-child(2) &-glow:nth-child(3) {
			top: 41%;
			right: 54.5%; }
		&:nth-child(2) &-glow:nth-child(4) {
			top: 55%;
			left: 35%; }

		&:nth-child(3) &-glow:nth-child(2) {
			top: 10%;
			left: 49%; }
		&:nth-child(3) &-glow:nth-child(3) {
			top: 33%;
			right: 53%; }
		&:nth-child(3) &-glow:nth-child(4) {
			top: 58%;
			left: 35%; }

		&:nth-child(4) &-glow:nth-child(2) {
			top: 18%;
			left: 32%; }
		&:nth-child(4) &-glow:nth-child(3) {
			top: 18%;
			right: 35%; }
		&:nth-child(4) &-glow:nth-child(4) {
			top: 58%;
			left: 36.5%; }

		&:nth-child(5) &-glow:nth-child(2) {
			top: 1.5%;
			left: 35%; }
		&:nth-child(5) &-glow:nth-child(3) {
			top: 30%;
			right: 47%; }
		&:nth-child(5) &-glow:nth-child(4) {
			top: 55%;
			left: 45.5%; }

		&:nth-child(2) &-descr {
			margin-top: 10px; }

		&-img {
			display: inline-block;
			vertical-align: middle;
			&_1 {
				@include s('why-us-1'); }
			&_2 {
				@include s('why-us-2'); }
			&_3 {
				@include s('why-us-3'); }
			&_4 {
				@include s('why-us-4'); }
			&_5 {
				@include s('why-us-5'); } }
		&-glow {
			position: absolute;
			z-index: 2; }
		&-parallax {
			@include s('glow'); }
		&-count {
			position: absolute;
			right: 25px;
			top: 156px;
			height: 53px;
			padding: 0 18px;
			@include f('pn-900', 35);
			line-height: 57px;
			letter-spacing: 0.05em;
			color: $white;
			border-radius: 20px;
			background: transparentize(#78c698, .1);
			&_wide {
				padding: 0 24px; } }

		&-descr {
			max-width: 198px;
			margin: 18px auto 0;
			font-size: 20px;
			line-height: 25px; } }
	&__btn {
		position: relative;
		z-index: 2; }

	&_default {
		padding: 80px 0 88px; } }
