/* ===================================
	 news-block
	 @mixin ================================== */
.news-block__actions {
  display: flex;
  align-items: center;
  margin-bottom: 50px;
}

.news-block__actions-btn {
  margin-right: 28px;
}

.news-block__actions-link {
  font-family: "ProximaNova-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  color: #0f2740;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border-bottom: 1px solid rgba(15, 39, 64, 0.3);
  transition: border-color 0.3s;
}

.no-touch .news-block__actions-link:hover {
  border-bottom-color: transparent;
}

.news-block__slider {
  padding-bottom: 80px;
  display: none;
}

.news-block__slider.slick-initialized {
  display: block;
}

.news-block__slider .slick-list {
  margin: 0 -15px;
}

.news-block__slider-item {
  margin: 0 15px;
}

.news-block__slider .slick-dots {
  display: flex;
  position: absolute;
  left: 50%;
  bottom: 7px;
  z-index: 2;
  transform: translateX(-50%);
}

.news-block__slider .slick-dots li {
  position: relative;
  width: 14px;
  height: 14px;
  border: 3px solid transparent;
  border-radius: 50%;
  cursor: pointer;
  transition: border-color .7s ease-in-out;
}

.news-block__slider .slick-dots li:not(:first-child) {
  margin-left: 3px;
}

.news-block__slider .slick-dots li.slick-active {
  border-color: #26b2b5;
}

.news-block__slider .slick-dots li.slick-active button {
  background: transparent;
}

.news-block__slider .slick-dots button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  width: 10px;
  height: 10px;
  background: #e0f4f5;
  border-radius: 50%;
}

.news-block_default {
  padding: 103px 0;
}

